// Class Prefix
$pluginPrefix: "coca-image-compare" !default;

// Placeholders
%handle-content {
	position: absolute;
	z-index: 30;
	display: block;
	content: " ";
	background: var(--coca-image-compare-handle-color);
	// @include box-shadow(--coca-image-compare-handle-box-shadow);
}

%handle-position-horizontal {
	left: 50%;
	width: var(--coca-image-compare-handle-stroke);
	height: 9999px;
	margin-left: calc(-1 * var(--coca-image-compare-handle-stroke) / 2);
}

%handle-position-vertical {
	top: 50%;
	width: 9999px;
	height: var(--coca-image-compare-handle-stroke);
	margin-top: calc(-1 * var(--coca-image-compare-handle-stroke) / 2);
}

%absolute-wh-position {
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
}

%overlay-transition-duration {
	transition-duration: 0.5s;
}

%label-transition-property {
	transition-property: opacity;
}

%label-text {
	font-size: var(--coca-image-compare-overlay-label-font-size);
	color: var(--coca-image-compare-overlay-label-color);
	letter-spacing: var(--coca-image-compare-overlay-label-letter-spacing);
}

%label-structure {
	position: absolute;
	padding: 0 var(--coca-image-compare-overlay-label-padding);
	line-height: var(--coca-image-compare-overlay-label-height);
	background: var(--coca-image-compare-overlay-label-bg);
	border-radius: var(--coca-image-compare-label-radius);
}

%label-position-horizontal {
	top: 50%;
	margin-top: calc(-1 * var(--coca-image-compare-overlay-label-height) / 2);
}

%label-position-vertical {
	left: 50%;
	width: var(--coca-image-compare-overlay-label-width);
	margin-left: calc(-1 * var(--coca-image-compare-overlay-label-width) / 2);
	text-align: center;
}

%css-triangle {
	position: absolute;
	width: 0;
	height: 0;
	border: var(--coca-image-compare-handle-triangle-size) inset transparent;
}

%css-triangle-horizontal {
	@extend %css-triangle;
	top: 50%;
	margin-top: calc(-1 * var(--coca-image-compare-handle-triangle-size));
}

%css-triangle-vertical {
	@extend %css-triangle;
	left: 50%;
	margin-left: calc(-1 * var(--coca-image-compare-handle-triangle-size));
}

// Wrapper
.#{$pluginPrefix}-wrapper {
	// Handle Styles
	--coca-image-compare-handle-color: #fff;
	--coca-image-compare-handle-stroke: 3px;
	--coca-image-compare-handle-circle-width: 40px;
	--coca-image-compare-handle-circle-height: 40px;
	--coca-image-compare-handle-box-shadow: 0 0 12px rgba(51, 51, 51, 0.5);
	--coca-image-compare-handle-triangle-color: var(--coca-image-compare-handle-color);
	--coca-image-compare-handle-triangle-size: 6px;
	--coca-image-compare-handle-triangle-position: 5px;
	--coca-image-compare-handle-radius: 1000px;

	// Overlay Styles
	--coca-image-compare-overlay-bg: rgba(0, 0, 0, 0.5);
	--coca-image-compare-overlay-label-color: #fff;
	--coca-image-compare-overlay-label-bg: rgba(255, 255, 255, .2);
	--coca-image-compare-overlay-label-height: 38px;
	--coca-image-compare-overlay-label-width: 90px;
	--coca-image-compare-overlay-label-padding: 20px;
	--coca-image-compare-overlay-label-font-size: 13px;
	--coca-image-compare-overlay-label-letter-spacing: 0.1em;
	--coca-image-compare-label-radius: 2px;

	.#{$pluginPrefix}-handle::after,
	.#{$pluginPrefix}-handle::before {
		transition: 300ms ease-in-out;
	}

	// Handle Styles
	&:not(.handle-bottom) .#{$pluginPrefix}-handle {
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 40;
		width: var(--coca-image-compare-handle-circle-width);
		height: var(--coca-image-compare-handle-circle-height);
		margin-top: calc(-1 * calc(var(--coca-image-compare-handle-circle-height) / 2) - var(--coca-image-compare-handle-stroke));
		margin-left: calc(-1 * calc(var(--coca-image-compare-handle-circle-width) / 2) - var(--coca-image-compare-handle-stroke));
		cursor: pointer;
		border: var(--coca-image-compare-handle-stroke) solid var(--coca-image-compare-handle-color);
		border-radius: var(--coca-image-compare-handle-radius);
		box-shadow: var(--coca-image-compare-handle-box-shadow);
		transition: none !important;
	}

	&.handle-bottom {
		&:not(.#{$pluginPrefix}-vertical) .#{$pluginPrefix}-container {
			margin-bottom: 25px;
		}

		.#{$pluginPrefix}-container {
			overflow: inherit;
		}

		.#{$pluginPrefix}-handle-wrapper {
			position: absolute;
			bottom: -22px;
			z-index: 10;
			width: 100%;
			height: 10px;
			margin-right: 0;
			background: rgba(0, 0, 0, 0.25);
			border-radius: 30px;
			//overflow: hidden;
		}

		.#{$pluginPrefix}-handle {
			position: absolute;
			top: inherit;
			bottom: 0;
			left: 50%;
			z-index: 99999;
			width: 45px;
			height: 10px;
			padding: 0;
			margin: 0 0 0 -22px;
			cursor: pointer;
			background: #fff;
			border: 0;
			border-radius: 30px;
			box-shadow: var(--coca-image-compare-handle-box-shadow);
			transition: none !important;
		}

		&.#{$pluginPrefix}-vertical .#{$pluginPrefix}-handle-wrapper {
			right: -22px;
			bottom: 0;
			width: 10px;
			height: 100%;
		}

		&.#{$pluginPrefix}-vertical .#{$pluginPrefix}-handle {
			left: 0;
			width: 10px;
			height: 45px;
			margin: -22px 0 0;
		}
	}
}

// Container
.#{$pluginPrefix}-container {
	position: relative;
	z-index: 0;
	box-sizing: content-box;
	overflow: hidden;
	-webkit-user-select: none;
	-moz-user-select: none;
	animation: fadeIn 800ms;

	@keyframes fadeIn {

		0% {
			opacity: 0;
		}

		100% {
			opacity: 1;
		}
	}

	img {
		position: absolute;
		top: 0;
		display: block;
		max-width: 100%;
		height: auto;
	}

	&.active .#{$pluginPrefix}-overlay,
	&.active:hover .#{$pluginPrefix}-overlay {
		background: rgba(#000, 0);
	}

	&.active .#{$pluginPrefix}-overlay {
		.#{$pluginPrefix}-before-label,
		.#{$pluginPrefix}-after-label {
			opacity: 0;
		}
	}

	&.active:hover .#{$pluginPrefix}-overlay {
		.#{$pluginPrefix}-before-label,
		.#{$pluginPrefix}-after-label {
			opacity: 1;
		}
	}

	&.has-indicator .#{$pluginPrefix}-overlay {
		background-color: transparent !important;
	}

	* {
		box-sizing: content-box;
	}

	// working with indicator image
	& .#{$pluginPrefix}-overlay-image {
		z-index: 25;
		width: 100%;
		height: 100%;
		visibility: hidden;
	}

	&:hover .#{$pluginPrefix}-overlay-image {
		visibility: visible;
	}
}

// Before Label
.#{$pluginPrefix}-before-label {
	@extend %absolute-wh-position;
	@extend %label-transition-property;
	@extend %overlay-transition-duration;
	opacity: 0;

	&:before {
		@extend %label-structure;
		@extend %label-text;
		content: attr(data-content);
	}
}

// After Label
.#{$pluginPrefix}-after-label {
	@extend %absolute-wh-position;
	@extend %label-transition-property;
	@extend %overlay-transition-duration;
	opacity: 0;

	&:before {
		@extend %label-structure;
		@extend %label-text;
		content: attr(data-content);
	}
}

// Horizontal Labels
.#{$pluginPrefix}-horizontal {
	.#{$pluginPrefix}-after-label:before,
	.#{$pluginPrefix}-before-label:before {
		@extend %label-position-horizontal;
	}

	.label-horizontal-top & {
		.#{$pluginPrefix}-before-label:before,
		.#{$pluginPrefix}-after-label:before {
			top: 10px;
			margin-top: 0;
			transform: translateY(0);
		}
	}

	.label-horizontal-bottom & {
		.#{$pluginPrefix}-before-label:before,
		.#{$pluginPrefix}-after-label:before {
			top: auto;
			bottom: 10px;
			transform: translateY(0);
		}
	}


	.#{$pluginPrefix}-before-label:before {
		left: 10px;
	}

	.#{$pluginPrefix}-after-label:before {
		right: 10px;
	}
}

// Vertical Labels
.#{$pluginPrefix}-vertical {
	.#{$pluginPrefix}-before-label:before,
	.#{$pluginPrefix}-after-label:before {
		@extend %label-position-vertical;
	}


	.label-vertical-left & {
		.#{$pluginPrefix}-before-label:before,
		.#{$pluginPrefix}-after-label:before {
			left: 10px;
			margin-left: 0;
			transform: translateX(0);
		}
	}

	.label-vertical-right & {
		.#{$pluginPrefix}-before-label:before,
		.#{$pluginPrefix}-after-label:before {
			right: 10px;
			left: auto;
			transform: translateX(0);
		}
	}


	.#{$pluginPrefix}-before-label:before {
		top: 10px;
	}

	.#{$pluginPrefix}-after-label:before {
		bottom: 10px;
	}
}

// Overlay
.#{$pluginPrefix}-overlay {
	@extend %absolute-wh-position;
	@extend %overlay-transition-duration;
	z-index: 25;
	background: rgba(#000, 0);
	transition-property: background;

	&:hover {

		&:not(.no-overlay) {
			background: var(--coca-image-compare-overlay-bg);
		}

		.#{$pluginPrefix}-after-label {
			opacity: 1;
		}

		.#{$pluginPrefix}-before-label {
			opacity: 1;
		}
	}
}

.#{$pluginPrefix}-before {
	z-index: 20;
}

.#{$pluginPrefix}-after {
	z-index: 10;
}

// Handle Styles
.#{$pluginPrefix}-horizontal:not(.handle-bottom) .#{$pluginPrefix}-handle {

	&:before {
		@extend %handle-content;
		@extend %handle-position-horizontal;
		bottom: 50%;
		margin-bottom: calc(calc(var(--coca-image-compare-handle-circle-height) / 2) + var(--coca-image-compare-handle-stroke));
		box-shadow: 0 var(--coca-image-compare-handle-stroke) 0 var(--coca-image-compare-handle-color), var(--coca-image-compare-handle-box-shadow);
	}

	&:after {
		@extend %handle-content;
		@extend %handle-position-horizontal;
		top: 50%;
		margin-top: calc(var(--coca-image-compare-handle-circle-height) / 2);
		//margin-top: calc(calc(var(--coca-image-compare-handle-circle-height) / 2) + var(--coca-image-compare-handle-stroke));
		box-shadow: 0 var(--coca-image-compare-handle-stroke) 0 var(--coca-image-compare-handle-color), var(--coca-image-compare-handle-box-shadow);
	}
}

// Vertical Handle Styles
.#{$pluginPrefix}-vertical:not(.handle-bottom) .#{$pluginPrefix}-handle {

	&:before {
		@extend %handle-content;
		@extend %handle-position-vertical;
		left: 50%;
		margin-left: calc(var(--coca-image-compare-handle-circle-width) / 2);
		//margin-left: calc(calc(var(--coca-image-compare-handle-circle-width) / 2) + var(--coca-image-compare-handle-stroke));
		box-shadow: var(--coca-image-compare-handle-stroke) 0 0 var(--coca-image-compare-handle-color), var(--coca-image-compare-handle-box-shadow);
	}

	&:after {
		@extend %handle-content;
		@extend %handle-position-vertical;
		right: 50%;
		margin-right: calc(var(--coca-image-compare-handle-circle-width) / 2);
		//margin-right: calc(calc(var(--coca-image-compare-handle-circle-width) / 2) + var(--coca-image-compare-handle-stroke));
		box-shadow: calc(-1 * var(--coca-image-compare-handle-stroke)) 0 0 var(--coca-image-compare-handle-color), var(--coca-image-compare-handle-box-shadow);
	}
}


// Left Handle
.#{$pluginPrefix}-left-arrow {
	left: 50%;
	margin-left: calc(-1 * calc(var(--coca-image-compare-handle-triangle-size) * 2) - var(--coca-image-compare-handle-triangle-position));
	border-right: var(--coca-image-compare-handle-triangle-size) solid var(--coca-image-compare-handle-triangle-color);
	@extend %css-triangle-horizontal;
}

// Right Handle
.#{$pluginPrefix}-right-arrow {
	right: 50%;
	margin-right: calc(-1 * calc(var(--coca-image-compare-handle-triangle-size) * 2) - var(--coca-image-compare-handle-triangle-position));
	border-left: var(--coca-image-compare-handle-triangle-size) solid var(--coca-image-compare-handle-triangle-color);
	@extend %css-triangle-horizontal;
}

// Up Handle
.#{$pluginPrefix}-up-arrow {
	top: 50%;
	margin-top: calc(-1 * calc(var(--coca-image-compare-handle-triangle-size) * 2) - var(--coca-image-compare-handle-triangle-position));
	border-bottom: var(--coca-image-compare-handle-triangle-size) solid var(--coca-image-compare-handle-triangle-color);
	@extend %css-triangle-vertical;
}

// Down Handle
.#{$pluginPrefix}-down-arrow {
	bottom: 50%;
	margin-bottom: calc(-1 * calc(var(--coca-image-compare-handle-triangle-size) * 2) - var(--coca-image-compare-handle-triangle-position));
	border-top: var(--coca-image-compare-handle-triangle-size) solid var(--coca-image-compare-handle-triangle-color);
	@extend %css-triangle-vertical;
}
